<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>column</td>
<td>固定多少列</td>
<td>number</td>
<td>正整数</td>
<td>-</td>
</tr>
<tr>
<td>setTable</td>
<td>一些设置</td>
<td>Object</td>
<td>{cellWidth:100}</td>
<td>-</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>sort</td>
<td>排序的回调</td>
<td>{sort: (true/false), index: '点击的列'}</td>
</tr>
</tbody>
</table>
</section></template>

<script>
export default {
  data() {
	return {
	  theadList: [
	    "姓名",
	    "英文姓名",
	    "性别",
	    "所属分校",
	    "客户状态",
	    "试听状态",
	  ],
	  studentInfo: [{
	    name: 'John Brown',
	    age: 18,
	    address: 'New York No. 1 Lake Park',
	    province: 'America',
	    city: 'New York',
	    zip: 100000
	   },{
	    name: 'Jim Green',
	    age: 24,
	    address: 'Washington, D.C. No. 1 Lake Park',
	    province: 'America',
	    city: 'Washington, D.C.',
	    zip: 100000
	   },{
	    name: 'Joe Black',
	    age: 30,
	    address: 'Sydney No. 1 Lake Park',
	    province: 'Australian',
	    city: 'Sydney',
	    zip: 100000
	   },{
	    name: 'Jon Snow',
	    age: 26,
	    address: 'Ottawa No. 2 Lake Park',
	    province: 'Canada',
	    city: 'Ottawa',
	    zip: 100000
	  }],
	  tableInit: {
      cellWidth: 100
    },
    sorts:'icon-iconset0413'
	  }
	},
  methods: {
	  sort(s) {
	  this.sorts = s.sort == 1 ? 'icon-iconset0413' : 'icon-iconset0414';
	    alert(JSON.stringify(s));
	  }
  }
};
</script>


