  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>标签四<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span>&gt;</span>标签五<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">ishover</span>&gt;</span>hover1<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">ishover</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"info"</span>&gt;</span>hover3<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">ishover</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>标签二<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">ishover</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"success"</span>&gt;</span>hover2<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">ishover</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"warning"</span>&gt;</span>hover4<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">x-tag</span> <span class="hljs-attr">ishover</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"danger"</span>&gt;</span>hover5<span class="hljs-tag">&lt;/<span class="hljs-name">x-tag</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>可移除标签</h3>
<p>设置<code v-pre="" class="code_inline">closable</code>属性可以定义一个标签是否可移除。默认的标签移除时会附带渐变动画，它接受一个Boolean，true 为关闭。</p>
<div class="demo-block">
<x-tag :key="tag.name" v-for="(tag, index) in dynamicTags" :ishover="index < 2" closable="" :color="tag.color" :name="tag" @on-close="handleClose">
  {{tag.name}}
</x-tag>
</div>

<demo-block> <div slot="desc">
<template>
  <x-tag :key="tag.name" v-for="(tag, index) in dynamicTags" :ishover="index < 2" closable="" :color="tag.color" :name="tag" @on-close="handleClose">
    {{tag.name}}
  </x-tag>
</template>


