      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">dynamicTags</span>: [{
           <span class="hljs-attr">name</span>: <span class="hljs-string">'标签一'</span>,
           <span class="hljs-attr">color</span>:<span class="hljs-string">'primary'</span>
        },{
           <span class="hljs-attr">name</span>: <span class="hljs-string">'标签二'</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'success'</span> 
        }, {
           <span class="hljs-attr">name</span>: <span class="hljs-string">'标签三'</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'info'</span> 
        }, {
           <span class="hljs-attr">name</span>: <span class="hljs-string">'标签四'</span>,
           <span class="hljs-attr">color</span>: <span class="hljs-string">'warning'</span>           
        }, {
            <span class="hljs-attr">name</span>: <span class="hljs-string">'标签五'</span>,
            <span class="hljs-attr">color</span>: <span class="hljs-string">'danger'</span>
        }]
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleClose(event, name) {
        <span class="hljs-built_in">console</span>.log(event, name);
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h2>API</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>用于触发关闭事件时的回调</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>color</td>
<td>类型</td>
<td>String</td>
<td><code v-pre="" class="code_inline">primary</code>, <code v-pre="" class="code_inline">success</code>, <code v-pre="" class="code_inline">error</code>, <code v-pre="" class="code_inline">warning</code>, <code v-pre="" class="code_inline">info</code></td>
<td>primary</td>
</tr>
<tr>
<td>ishover</td>
<td>是否有hover效果</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>closable</td>
<td>是否可关闭</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>Tag 事件</h2>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-close</td>
<td>点击关闭按钮时触发</td>
<td>event,name</td>
</tr>
</tbody>
</table>
</section></template>
<style>
  .x-tag{
    margin: 10px;
  }
</style>
<script>
export default {
    data() {
      return {
        dynamicTags: [{
           name: '标签一',
           color:'primary'
        },{
           name: '标签二',
           color: 'success' 
        }, {
           name: '标签三',
           color: 'info' 
        }, {
           name: '标签四',
           color: 'warning'           
        }, {
            name: '标签五',
            color: 'danger'
        }]
      };
    },
    methods: {
      handleClose(event, name) {
        console.log(event, name);
      }
    }
  }
</script>


